* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  background-image: url("vegetable-bg.jpg");
  background-size: cover;
}

#container {
  padding: 30px 15px;
}

.notes {
  font-size: 14px;
  font-weight: bold;
}

.title {
  margin: 0.55em 0 0.28em;
  padding: 0.44em 0 0 0;
}

#leftArea {
  padding: 0 20px;
  float: left;
}

#textArea {
  padding: 20px 0 0 0;
  margin: 0 0 10px 0;
}

.error {
  text-align: justify;
}

#demoAnalysis {
  margin: 20px 0;
  padding: 6px 12px;
}

.table {
  font-size: 12px;
  margin: 50px 0 20px;
}

#rightArea {
  float: left;
}

.fact-container {
  margin: 10px 0;
}

#factCard {
  border: 1px solid black;
  padding: 0 20px 5px;
  margin: 40px 10px 0;
}

#factTitle {
  border-bottom: 10px solid black;
}

.calories {
  font-weight: bold;
  font-size: 35px;
}

#factTable {
  width: 100%;
}

.small-info {
  font-size: 11px;
}

.thickBorder {
  border-top: 5px solid black;
}

.factItems {
  border-top: 1px solid black;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
}

.factPercentage {
  border-top: 1px solid black;
  text-align: right;
  font-size: 14px;
  font-weight: bold;
}
